import React, { useState, useEffect } from 'react';
import styles from './Header.module.css';
import { CSSTransition } from 'react-transition-group';

export default function Header(props) {

    const [visibility, setVisibility] = useState(false);
    const [scrolled, setScrolled] = useState(false);
    const [burger, setBurger] = useState(false);
    const [navBtns, setNavBtns] = useState([false, false]);
    const [language, setLanguage] = useState();
    
    
    useEffect(() => setVisibility(true), []);
    useEffect(() => onNavBtn(props.slide), [props.slide]);
    useEffect(() => setLanguage(props.language), [props.language]);

    window.onscroll = () => {
        window.pageYOffset > 50 ? setScrolled(true) : setScrolled(false);
    }

    const onBurger = () => {
        if(window.innerWidth < 800){
            if(burger) document.querySelector('body').classList.remove(styles.noScroll);
            else document.querySelector('body').classList.add(styles.noScroll);
        }
        setBurger(!burger);
    }

    const onNavBtn = (slide) => {
        if(slide == 0) setNavBtns([false, false]);
        if(slide == 1) setNavBtns([true, false]);
        if(slide == 2) setNavBtns([false, true]);
        props.changeSlider(slide);
        props.changePlayer(false);
    }

    const onChangeLanguage = () => props.changeLanguage(!language);

    return (

        <header className={styles.header}>
            <CSSTransition 
                in={props.player} 
                classNames={{enterActive: styles.wrapperEnterDoneByIntroPlayer, enterDone: styles.wrapperEnterDoneByIntroPlayer}} 
                timeout={0}>
                <CSSTransition 
                    in={scrolled} 
                    classNames={{enterActive: styles.wrapperEnterDone, enterDone: styles.wrapperEnterDone}} 
                    timeout={0}>
                    <div className={styles.header_wrapper}>

                        <div className={styles.logo_name}>
                            <a href=".">
                                <CSSTransition 
                                    in={visibility} 
                                    classNames={{enterActive: styles.azhypaEnterActive, enterDone: styles.azhypaEnterDone}} 
                                    timeout={0}>
                                    <span className={styles.azhypa}>AZHYPA</span>
                                </CSSTransition>
                            </a>
                            <a href="./">
                                <CSSTransition 
                                    in={visibility} 
                                    classNames={{enterActive: styles.mediaCenterEnterActive, enterDone: styles.mediaCenterEnterDone}} 
                                    timeout={0}>
                                    <span className={styles.mediaCenter}>MEDIA CENTER</span>
                                </CSSTransition>
                            </a>
                        </div>

                        <CSSTransition
                            in={burger} 
                            classNames={{enterActive: styles.navEnterDone, enterDone: styles.navEnterDone}} 
                            timeout={0}>
                            <nav className={styles.header_nav}>
                                <ul className={styles.header_list}>
                                    <li className={styles.header_item}>
                                        <CSSTransition 
                                            in={visibility}
                                            classNames={{enterActive: styles.linkEnterActive, enterDone: styles.linkEnterDone}}
                                            timeout={0}>
                                            <CSSTransition 
                                                in={navBtns[0]}
                                                classNames={{enterActive: styles.navBtnDone, enterDone: styles.navBtnDone}}
                                                timeout={0}>
                                                <a 
                                                    onClick={() => {onNavBtn(1); window.scrollTo(0, 0); onBurger()}} 
                                                    href="#about" 
                                                    aria-disabled="true" 
                                                    className={styles.header_link}> 
                                                    About
                                                </a>
                                            </CSSTransition>
                                        </CSSTransition>
                                    </li>
                                    <li className={styles.header_item}>
                                        <CSSTransition 
                                            in={visibility} 
                                            classNames={{enterActive: styles.linkEnterActive, enterDone: styles.linkEnterDone}} 
                                            timeout={0}>
                                            <CSSTransition 
                                                in={navBtns[1]}
                                                classNames={{enterActive: styles.navBtnDone, enterDone: styles.navBtnDone}}
                                                timeout={0}>
                                                <a 
                                                    onClick={() => {onNavBtn(2); window.scrollTo(0, 0); onBurger()}} 
                                                    href="#contact" 
                                                    className={styles.header_link}> 
                                                    Contacts
                                                </a>
                                            </CSSTransition>
                                        </CSSTransition>
                                    </li>
                                    <li className={styles.header_item}>
                                        <CSSTransition 
                                            in={visibility} 
                                            classNames={{enterActive: styles.linkEnterActive, enterDone: styles.linkEnterDone}} 
                                            timeout={0}>
                                            <div onClick={() => {onChangeLanguage(); onBurger()}} className={styles.language}>
                                                <CSSTransition 
                                                    in={language}
                                                    classNames={{enterActive: styles.navBtnLanguageDone, enterDone: styles.navBtnLanguageDone}}
                                                    timeout={0}>
                                                    <span>RU</span>
                                                </CSSTransition>
                                                <span>/</span>
                                                <CSSTransition 
                                                    in={!language}
                                                    classNames={{enterActive: styles.navBtnLanguageDone, enterDone: styles.navBtnLanguageDone}}
                                                    timeout={0}>
                                                    <span>EN</span>
                                                </CSSTransition>
                                            </div>
                                        </CSSTransition>
                                    </li>
                                </ul>
                            </nav>
                        </CSSTransition>

                        <CSSTransition
                            in={visibility} 
                            classNames={{enterActive: styles.burgerShowEnterDone, enterDone: styles.burgerShowEnterDone}} 
                            timeout={0}>
                            <CSSTransition
                                in={burger} 
                                classNames={{enterActive: styles.burgerEnterDone, enterDone: styles.burgerEnterDone}} 
                                timeout={0}>
                                <div onClick={onBurger} className={styles.burger}>
                                    <CSSTransition 
                                        in={burger} 
                                        classNames={{enterActive: styles.burgerIconEnterDone, enterDone: styles.burgerIconEnterDone}} 
                                        timeout={0}>
                                        <span className={styles.burgerIcon}></span>
                                    </CSSTransition>
                                </div>
                            </CSSTransition>
                        </CSSTransition>
                    
                    </div>
                </CSSTransition>
            </CSSTransition>
            

            <CSSTransition 
                in={scrolled} 
                classNames={{enterActive: styles.gredientEnterDone, enterDone: styles.gredientEnterDone}} 
                timeout={0}>
                <div className={styles.header_gredient}></div>
            </CSSTransition>
        </header>
    );
    
}
