import React, { useState, useEffect } from 'react';
import styles from './Slot.module.css';
import { CSSTransition } from 'react-transition-group';

export default function Slot({ index, id, name, img, videoPrev, video, videoDescription, handleClick, player }) {

    const [isActive, setIsActive] = useState(false);
    const [isHover, setIsHover] = useState(false);


    useEffect(() => {
        if(player !== name) setIsActive(false);
    }, [player]);


    let tagVideo;
    if(isHover) tagVideo = <video src={ videoPrev } muted autoPlay loop className={styles.slotVideo}></video>
    else tagVideo = <video className={styles.slotVideo}></video>
 
    return(
        <a href={ id } className={styles.slotRef}
            onMouseEnter={() => setIsHover(true)}
            onMouseLeave={() => setIsHover(false)}
            onClick={() => {
                handleClick(index, name, video, videoDescription, !isActive);
                setIsActive(!isActive);
            }}>

            <div className={styles.slotContent}>
                <CSSTransition
                    in={isHover} 
                    classNames={{enterActive: styles.slotImgDone, enterDone: styles.slotImgDone}} 
                    timeout={0}>
                    <img src={ img } className={styles.slotImg} alt={ id }></img>
                </CSSTransition>
                <CSSTransition
                    in={isHover} 
                    classNames={{enterActive: styles.slotVideoDone, enterDone: styles.slotVideoDone}} 
                    timeout={0}>
                    {tagVideo}
                </CSSTransition>
                <CSSTransition
                    in={isHover}
                    classNames={{enterActive: styles.slotTextDone, enterDone: styles.slotTextDone}} 
                    timeout={0}>
                    <div className={styles.slotText}> { name } </div>
                </CSSTransition>
            </div>
        </a>
    );
    
}