import "./App.css";
import React, { useState, useEffect } from 'react';
import Header from "./components/Header/Header.js";
import Intro from "./components/Intro/Intro.js";
import ContentContainer from "./components/ContentContainer/ContentContainer.js";
import Footer from "./components/Footer/Footer.js";


const checkHash = () => {
  if(window.location.hash === '#about') return 1;
  else if(window.location.hash === '#contact') return 2;
  else return 0;
}

const checkRegion = () => {

  return true;
}


function App() {

  const [introSlide, setIntroSlide] = useState(() => checkHash());
  const [introPlayer, setIntroPlayer] = useState(false);
  const [content, setContent] = useState([false, false]);
  const [language, setLanguage] = useState(() => checkRegion());


  const onchangeSlider = (slide) => setIntroSlide(slide);
  const onchangePlayer = (player) => setIntroPlayer(player);
  const onchangeContent = (content) => setContent(content);
  const onChangeLanguage = () => setLanguage(!language);


  return (
    <div className="App">

      <div id="cursor"><img/></div>
      
      <Header language={language} changeLanguage={onChangeLanguage} changeSlider={onchangeSlider} slide={introSlide} changePlayer={onchangePlayer} player={introPlayer} ></Header>
      <Intro changeSlider={onchangeSlider} slide={introSlide} changePlayer={onchangePlayer} player={introPlayer} changeContent={onchangeContent}></Intro>
      <ContentContainer content={content} ></ContentContainer>
      <Footer></Footer>

      
    </div>
  );
}

export default App;
