import React from 'react';
import styles from './Course.module.css'

export default function Course({product, name, img, description, available, duration, }) {


    return(
        <div className={styles.wrapper}>
            <div>
                <img src={ img }></img>
                <div className={styles.container}>
                    <h2>{ product }</h2>
                    <h1>{ name }</h1>
                    <div className={styles.description}>
                        {description.p.map((text, index) => <p key={index}>{ text }</p>)}
                    </div>
                    <ul>
                        {description.li.map((text, index) => <li key={index}>{ text }</li>)}
                    </ul>
                </div>
            </div>
            
            <div>
                <div className={styles.info}>
                    <div className={styles.available}>
                        <p>{ available }</p>
                        <p> Available </p>
                    </div>
                    <div className={styles.duration}>
                        <p>
                            <span>{ duration }</span>
                            <span> min </span>
                        </p>
                        <p> Duration </p>
                    </div>
                </div>
                <a href='#'>
                    <button className={styles.btn}>improve your content</button>
                </a>
            </div>
                
        </div>
    );

    
}

