import React, { useState, useEffect } from 'react';
import styles from './ContentContainer.module.css';
import { CSSTransition } from 'react-transition-group';
import Slot from '../Slot/Slot.js'
import SlotPlayer from '../SlotPlayer/SlotPlayer.js'
import Course from '../Course/Course.js';
import { slots } from '../slots/storage.js';
import { courses } from '../courses/storage.js';



const getPlayerRow = (index) => {
    let rowNum;
    let screenWidth = window.innerWidth;
    
    if(screenWidth < 520) rowNum = index+2;
    if(screenWidth > 519 && screenWidth < 1333){
      if(index < 2) rowNum = 2;
      if(index < 4 && index > 1) rowNum = 3;
      if(index < 6 && index > 3) rowNum = 4;
      if(index < 8 && index > 5) rowNum = 5;
      if(index < 10 && index > 7) rowNum = 6;
      if(index < 12 && index > 9) rowNum = 7;
      if(index < 14 && index > 11) rowNum = 8;
      if(index < 16 && index > 13) rowNum = 9;
      if(index < 18 && index > 15) rowNum = 10;
    }
    if(screenWidth > 1332){
      if(index < 3) rowNum = 2;
      if(index < 6 && index > 2) rowNum = 3;
      if(index < 9 && index > 5) rowNum = 4;
      if(index < 12 && index > 8) rowNum = 5;
      if(index < 15 && index > 11) rowNum = 6;
      if(index < 18 && index > 14) rowNum = 7;
    }
    return rowNum;
  }


export default function ContentContainer(props) {

    const [player, setPlayer] = useState({name: '', video: '', videoDescription: '', row: ''});
    const [isPlay, setIsPlay] = useState(false);
    const [content, setContent] = useState(props.content);


    useEffect(() => setContent(props.content), [props.content])

    const onSlotClick = (index, name, video, videoDescription, isActive) => {

        if(isActive) {
            setIsPlay(true);
            setPlayer({name: name, video: video, videoDescription: videoDescription, row: getPlayerRow(index)});
        }
        else {
            setIsPlay(false);
            setPlayer(0);
        }
    }


    let slotPlayer;
    if (isPlay) slotPlayer = <SlotPlayer name={player.name} video={player.video} videoDescription={player.videoDescription} row={player.row} /> 

    return(
        <main className={styles.main}>
            <div className={styles.wrapper}>
                <CSSTransition
                    in={content[1]} 
                    classNames={{enterActive: styles.sliderDone, enterDone: styles.sliderDone}} 
                    timeout={0}>
                    <div className={styles.slider}>
            
                        <CSSTransition
                            in={content[1]} 
                            classNames={{enterActive: styles.containerEnterActive, enterDone: styles.containerEnterDone}} 
                            timeout={0}>
                            <div className={styles.slotsContainer}>
                                {slots.map((slot, index) => 
                                    <Slot 
                                        key={index} 
                                        index={index}
                                        id={slot.id} 
                                        name={slot.name} 
                                        img={slot.img} 
                                        videoPrev={slot.videoPrev} 
                                        video={slot.video} 
                                        videoDescription={slot.videoDescription}
                                        handleClick={onSlotClick}
                                        player={player.name}>
                                    </Slot>
                                )}
                                {slotPlayer}
                            </div>
                        </CSSTransition>

                        <CSSTransition
                            in={content[0]} 
                            classNames={{enterActive: styles.containerEnterActive, enterDone: styles.containerEnterDone}} 
                            timeout={0}>
                            <div className={styles.coursesWrapper}>
                                <div className={styles.coursesContainer}>
                                    {courses.map((course, index) => 
                                        <Course 
                                            key={index} 
                                            product={course.product}
                                            name={course.name} 
                                            img={course.img} 
                                            description={course.description}
                                            available={course.available}
                                            duration={course.duration}>
                                        </Course>
                                    )}
                                </div>
                            </div>
                        </CSSTransition>

                        
                        
                    </div>
                </CSSTransition>
                
            </div>
        </main>
    
    );
    
}