import React from 'react';
import styles from './Footer.module.css';
import { instagram, linkedin, mail, telegram, tiktok, youtube, logo } from '../../shared/exports.js';

export default function Footer(props) {

    return(
        <footer className={styles.footer}>
             
            <div className={styles.footer_container}>
                <div className={styles.footer_grid}>
                    <ul className={styles.footer_column}>
                        <li className={styles.grid_item}>
                            <a href="https://www.linkedin.com/in/siamion-azhypa-3242171a3/">
                                <img src={ linkedin }></img>
                            </a>
                        </li>
                        <li className={styles.grid_item}>
                            <a href="https://www.youtube.com/c/AzhypaMediaCenter/videos">
                                <img src={ youtube }></img>
                            </a>
                        </li>
                        <li className={styles.grid_item}>
                            <a href="https://www.instagram.com/azhypa.mediacenter/">
                                <img src={ instagram }></img>
                            </a>
                        </li>
                        <li className={styles.grid_item}>
                            <a href="https://t.me/azhypa">
                                <img src={ telegram }></img>
                            </a>
                        </li>
                        <li className={styles.grid_item}>
                            <a href="https://in.tiktok.com/@azhypa">
                                <img src={ tiktok }></img>
                            </a>
                        </li>
                        <li className=" grid_item">
                            <a href="mailto:s.azhypa@gmail.com">
                                <img src={ mail }></img>
                            </a>
                        </li>
                    </ul>
                    <ul className={styles.footer_column}>
                        <li className={styles.grid_item}>
                            <img onClick={() => window.scrollTo(0, 0)} src={ logo } className={styles.footer_logo}></img>
                        </li>
                    </ul>
                    <ul className={styles.footer_column}>
                        <li className={styles.grid_item}>
                            <span> 2022 Azhypa Media Center. All rights reserved.</span>
                        </li>
                        <li className={styles.grid_item}>
                            <span> 1998-2022 Wargaming.net. All rights reserved.</span>
                        </li>
                        <li className={styles.grid_item}>
                            <span>Any unauthorized usage of these materials is prohibited.</span>
                        </li>
                    </ul>
                </div>
            </div>

        </footer>
    );
}