import React, { useEffect } from 'react';
import styles from './SlotPlayer.module.css';

export default function SlotPlayer({ name, video, videoDescription, row }) {

    useEffect(() => {
        if(name) window.scrollTo(0, document.getElementById('slotPlayer').getBoundingClientRect().top + window.pageYOffset);
    }, [name]);

    
    return(
        <div id='slotPlayer' className={styles.wrapper} style={{gridRow: row}}>
            <div className={styles.container}>
                <h1> { name } </h1>
                <div className={styles.video}>
                    <iframe 
                        src={ video } 
                        title="YouTube video player" 
                        allow="accelerometer; autoplay; clipboard-write; encrypte-media; gyroscope; picture-in-picture" 
                        allowFullScreen>
                    </iframe>
                </div>
                <div className={styles.text}>
                    <p><span> Production: </span><span> { videoDescription[0] } </span></p>
                    <p><span> Azhypas role: </span><span> { videoDescription[1] } </span></p>
                    <p><span> Engine: </span><span> { videoDescription[2] } </span></p>
                    <p><span> Software: </span><span> { videoDescription[3] } </span></p>
                </div>
            </div>
        </div>
    );
}